import React from 'react';
import './loading-start.css';
import logo from './1_objects.svg';

const SoundHouseLogoWithSubtitle = () => {
  return (
    <img
      style={{
        margin:24,
        zIndex:2,
      }}
       
        
     
      src={logo}
      alt="LOGO"
    />
  );
};

export default SoundHouseLogoWithSubtitle;



